@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@700&family=Roboto&display=swap);
.user-workout-container {
	width: 100%;
	padding: 5%;
}
.user-workout-container h1 {
	font-size: 1.6em;
	color: #0c2b5b;
}
.add-button {
	border: 0;
	border-radius: 20px;
	background-color: #aeebdc;
	color: #0c2b5b;
	font-weight: 700;
	padding: 10px 20px;
	margin: 25px 0px;
	font-family: 'Roboto', sans-serif;
	width: 100%;
	outline: none;
}
.add-button i {
	padding-right: 10px;
}
.table-container {
	border: 1px solid #767676;
	border-radius: 15px;
	padding: 10px 10px;
	font-size: 14px;
	background-color: #f7f7f7;
}
.table-container .column-header {
	display: none;
}
.table-row {
	display: flex;
	flex-direction: column;
	border-bottom: 1px solid #0c2b5b;
	padding: 15px 0px;
}
.table-container i {
	margin: 10px 30px 0px 5px;
}
.table-cell:not(.column-header) {
	padding: 5px 10px;
	width: 100%;
}
.red-icon {
	color: #d8272e;
}
.blue-icon {
	color: #0c2b5b;
}
.table-cell input:not([name='new-metric']),
.table-cell select {
	border-radius: 5px;
	border: 1px solid #0c2b5b;
	padding: 10px 15px;
	margin: 5px 0px;
	color: #767676;
	outline: none;
	width: 100%;
}

.table-cell input:not([name='new-metric']):focus,
.table-cell select:focus {
	border: 1px solid #aeebdc;
	border-radius: 5px;
	outline: none;
}
.metric-radios {
	display: inline-block;
}
.metric-radios label {
	padding: 0px 5px;
	font-size: 12px;
}
.save-button {
	border: 0;
	border-radius: 20px;
	background-color: #0c2b5b;
	color: #aeebdc;
	font-weight: 700;
	padding: 10px 20px;
	font-family: 'Roboto', sans-serif;
	margin-top: 10px;
}

@media all and (max-width: 600px) {
	.table-row div:before {
		font-weight: 700;
		color: #0c2b5b;
	}

	.table-row div:nth-of-type(1):before {
		content: 'Exercise: ';
	}
	.table-row div:nth-of-type(2):before {
		content: 'Date: ';
	}
	.table-row div:nth-of-type(3):before {
		content: 'Metric Tracked: ';
	}
}

@media all and (min-width: 600px) {
	.user-workout-container {
		padding: 8%;
	}
	.add-button {
		width: auto;
	}
	.table-container {
		padding: 15px 25px;
		font-size: 14px;
	}
	.table-container .column-header {
		display: inherit;
		font-weight: 700;
		color: #0c2b5b;
	}
	.table-row {
		display: grid;
		grid-template-columns: repeat(auto-fit, minmax(25%, 1fr)) 10%;
		justify-content: stretch;
	}
	.table-container i {
		margin: 0px 5px;
	}
	.table-cell {
		padding: 5px 10px;
	}
	.save-button {
		padding: 10px 20px;
		margin: auto;
	}
}

.progress-container {
	width: 100%;
	padding: 5%;
}
.progress-container h1 {
	font-size: 1.6em;
	color: #0c2b5b;
}

.hero-image {
	width: 100%;
	height: 80vh;
	background-image: url(/static/media/header-image.2025dbed.png);
	background-size: cover;
	background-position: top;
	background-repeat: no-repeat;
	position: relative;
}

.hero-text-box {
	position: absolute;
	width: 90%;
	top: 80%;
	left: 0;
	opacity: 0.97;
	z-index: 9;
	margin: 5%;
	padding: 75px;
	background-color: #0c2b5b;
	color: white;
}

.hero-text-box h1 {
	font-size: 2em;
	margin-bottom: 1em;
	color: #daf2ec;
}

.hero-text-box p {
	line-height: 1.5em;
	margin: 25px 0px;
}

.hero-button {
	margin-top: 15px;
	padding: 8px 15px;
	background-color: #aeebdc;
	border-radius: 10px;
	border: 0;
	color: #0c2b5b;
	font-weight: 700;
	font-size: 16px;
}

.hero-button:hover {
	text-decoration: none;
	background-color: #daf2ec;
}

@media all and (max-width: 600px) {
	.hero-text-box {
		padding: 35px;
	}
}

.auth-pages-container {
	width: 100%;
	padding: 12%;
}

.auth-pages-container h1 {
	font-size: 2em;
	color: #0c2b5b;
}

.form {
	padding: 10%;
	border-radius: 10px;
	margin-top: 25px;
	border: 1px solid #767676;
	background-color: #f7f7f7;
}

.form label {
	display: block;
	font-weight: 700;
	margin: 10px 0px;
	color: #0c2b5b;
}

.form input {
	display: block;
	border-radius: 5px;
	border: 1px solid #0c2b5b;
	padding: 10px 15px;
	margin: 5px 0px;
	color: #767676;
	width: 100%;
}

.form input:focus {
	border: 1px solid #aeebdc;
	border-radius: 5px;
	outline: none;
}

.button-container {
	text-align: right;
}

.form button {
	border: 0;
	border-radius: 20px;
	background-color: #aeebdc;
	color: #0c2b5b;
	font-weight: 700;
	padding: 10px 20px;
	margin-top: 20px;
	font-family: 'Roboto', sans-serif;
}

.form button:hover {
	text-decoration: none;
	background-color: #daf2ec;
}

.info-message {
	margin-top: 30px;
	text-align: center;
	color: #0c2b5b;
}

.info-message a {
	color: #0c2b5b;
	font-weight: 700;
}

@media all and (min-width: 600px) {
	.form input {
		margin-bottom: 30px;
	}
}

/* Global Styling */
* {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	box-sizing: border-box;
	padding: 0;
	margin: 0;
}
body {
	font-family: 'Roboto', sans-serif;
	font-size: 16px;
}
a {
	text-decoration: none;
}
a:hover {
	text-decoration: underline;
}
.mt-20 {
	margin-top: 20px;
}
.mr-20 {
	margin-right: 20px;
}
.mb-20 {
	margin-bottom: 20px;
}
.ml-20 {
	margin-left: 20px;
}
.pt-20 {
	padding-top: 20px;
}
.pr-20 {
	padding-right: 20px;
}
.pb-20 {
	padding-bottom: 20px;
}
.pl-20 {
	padding-left: 20px;
}
.clickable {
	cursor: pointer;
}
.header-base {
	font-family: 'Raleway', sans-serif;
	font-weight: 700;
	letter-spacing: 0.5px;
}
.font-blue {
	color: #0c2b5b;
}
.font-white {
	color: #ffffff;
}

/* App Component Styling */
nav {
	padding: 5px 20px;
}
nav a {
	color: #0c2b5b;
	font-weight: 700;
}
nav ul {
	list-style-type: none;
	margin: 0;
}
.menu {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;
}
.menu li {
	padding: 15px 5px;
	white-space: nowrap;
}
.logo a:hover {
	text-decoration: none;
}
.logo i {
	margin-right: 10px;
	color: #68d0b1;
}
.toggle {
	order: 1;
}
.logo a,
.toggle a {
	font-size: 22px;
}
.nav-item {
	width: 100%;
	text-align: center;
	order: 2;
	display: none;
}
.nav-item.button {
	order: 3;
}
.nav-item.active {
	display: block;
}

/* Desktop Nav */
@media all and (min-width: 600px) {
	.menu {
		justify-content: center;
	}
	.menu li {
		padding: 15px 10px;
	}
	.menu li.button {
		padding-right: 0;
	}
	.logo {
		order: 0;
		flex: 1 1;
	}
	.toggle {
		display: none;
	}
	.nav-item {
		display: inline-block;
		width: auto;
		order: 1;
	}
	.nav-item.button {
		width: auto;
		order: 2;
		display: inline-block;
	}
	.nav-item.button.secondary {
		border: 0;
	}
	.nav-item.button a {
		padding: 7.5px 15px;
		background-color: #aeebdc;
		border-radius: 10px;
	}
	.nav-item.button.secondary a {
		background: transparent;
	}
	.nav-item.button a:hover {
		text-decoration: none;
	}
	.nav-item.button:not(.secondary) a:hover {
		background-color: #daf2ec;
	}
	.nav-item.button.secondary a:hover {
		color: #aeebdc;
	}
}

